<template>
  <div class="mem_package" >
  <!-- <br> -->
  <p>{{msg}}</p>
  <br>
  <el-form :inline="true" :model="formInline" class="demo-form-inline">
    <el-form-item label="">
      <el-input  placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input  placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input  ></el-input>
    </el-form-item>
      <el-button type="success" class="el-icon-search"@click="onSubmit"></el-button>
    </el-form-item>
  </el-form>
  <!--  -->
  <div class="content">
  <el-table  :data="tableData"  style="width: 100%" >
    <el-table-column  prop="username" label=""  width="180"></el-table-column>
    <el-table-column  prop="accumulative_mem_package" label=""  width="180"></el-table-column>
    <el-table-column  prop="avaliable_mem_package" label=""  width="180"></el-table-column>
    <el-table-column label="">
          <template scope="scope">
            <el-button size="small" class="font-color" ><i class="fa fa-book" aria-hidden="true"></i></el-button>
              <i class="fa fa-exchange font-color" aria-hidden="true"></i>
            <el-button size="small" class="font-color" @click="cash_register(scope.$index, scope.row)"> </el-button>
          </template>
        </el-table-column>
      </el-table>
  </el-table>    
  </div>
  <!--  -->
  <el-dialog title="" v-model="dialogFormVisible" size="tiny" >
    <el-form ref="form" :model="form" label-width="120px">
      <div class="left"><i class="fa fa-id-card fa-2x" aria-hidden="true"></i><span class="space">{{formInline.username}}</span></div>
      <div  class="right"><span class="content_size">{{formInline.accumulative_mem_package}}</span></div>  <br><br><br>
      <el-form-item label="">
      <el-input  v-model="form.exchange_item" ></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="form.use_mem_package"  ></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input  type="textarea" v-model="form.tip" ></el-input>
    </el-form-item>
    <el-form-item class="center">
    <template scope="scope">
      <el-button type="primary" @click="handleSave()" :loading="editLoading"></el-button>
      <el-button  @click="dialogFormVisible = false"></el-button>
    </template>
    </el-form-item>
    </el-form> 
  </el-dialog>   
  <!--  -->
  <div class="block">
     <el-pagination
       @size-change="handleSizeChange"
       @current-change="handleCurrentChange"
       :current-page="currentPage1"
       :page-sizes="[10, 20, 30, 40]"
       :page-size="100"
       layout="total, sizes, prev, pager, next, jumper"
       :total="tableData.length">
     </el-pagination>
   </div>
   </div>
   </div>
</template>
<script >

export default {
  name: 'mem_package',
  data () {
    return {
      msg: '>',
      dialogFormVisible:false,
      editLoading:false,//
      currentPage1: 5,
      activeIndex: '1',
      formInline: {
        username:'',
        accumulative_mem_package:'',
        avaliable_mem_package:'',
      },
      form : {
        exchange_item:'',
        use_mem_package:'',
        tip:'',
      },
      tableData: [{
                 username:'',
                 accumulative_mem_package:2356,
                 avaliable_mem_package:562,
               }, {
                 username:'',
                 accumulative_mem_package:56,
                 avaliable_mem_package:5,
               }, {
                 username:'',
                 accumulative_mem_package:236,
                 avaliable_mem_package:52,
               }, {
                 username:'',
                 accumulative_mem_package:256,
                 avaliable_mem_package:52,
               }]
    }
  },
  methods: {
       onSubmit() {
         this.$message('~');
       },

       cash_register (index, row) {
         this.dialogFormVisible = true;
         this.formInline = Object.assign({}, row);
         this.table_index = index;
       },

       handleSave () {
         this.$confirm('', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           cancelButtonClass: 'cancel'
         }).then(() => {
          this.editLoading = true;//
          this.tableData[this.table_index] = this.formInline;
          this.tableData.splice(this.table_index, 1, this.formInline);
          this.$message({
             message: "",
             type: 'success'
           });
           this.editLoading = false;
           this.dialogFormVisible = false;
           console.log(this.form);
         })
      },
      handleSizeChange(val) {
      console.log(` ${val} `);
      },

      handleCurrentChange(val) {
      console.log(`: ${val}`);
      },
      handleSelect(key, keyPath) {
      console.log(key, keyPath);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.mem_package{
  padding: 20px;
}

.mem_package .content{
 /* width: 95%;*/
  height: 400px;
  background: white;
 /* margin: 0 auto;*/
}
.mem_package .block{
  text-align: center;
}
.mem_package .center{
  text-align: center;
}
.mem_package .font-color{
  color: #50bfff; 
   border:0px solid #50bfff;
}
.mem_package .left{
  float: left; 
  text-align: center;
  width: 50%;
}
.mem_package .right{
  float: right; 
  text-align: center;
  width: 50%;

}
.mem_package .center{
  text-align: center;
}

</style>



