<template>
  <div class="account" >
  <!-- <br> -->
  <p>{{msg}}</p>
  <br>
  <el-form :inline="true" :model="formInline" class="demo-form-inline">
    <el-form-item label="">
      <el-input v-model="formInline.account" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="formInline.region" placeholder="">
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="vip" value="vip"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formInline.user" placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="success" class="el-icon-search"@click="onSubmit"></el-button>
    </el-form-item> 
  </el-form>
  <!--  -->
  <div class="content">
  <el-table  :data="tableData"  style="width: 100%" >
    <el-table-column  prop="account"label=""  width="110">
    </el-table-column>
    <el-table-column  prop="style" label=""  width="150"></el-table-column>
    <el-table-column  prop="name" label=""  width="110"></el-table-column>
    <el-table-column  prop="share" label=""  width="130"></el-table-column>
    <el-table-column  prop="money" label=""  width="150"></el-table-column>
    <el-table-column  prop="deadline" label=""  width="130"></el-table-column>
    <el-table-column label="">
          <template scope="scope">
            <el-button size="small" type="success" @click="accountPay(scope.$index, scope.row)"> </el-button>
            <el-button size="small" type="success" @click="styleChange(scope.$index, scope.row)"> </el-button>
            <el-button size="small" type="success" @click="familyShare(scope.$index, scope.row)"> </el-button>
            <el-button size="small" type="danger"  @click="handleDelete(scope.$index, scope.row)"></el-button> 
          </template>
        </el-table-column>
      </el-table>
  </el-table>    
  
  </div>
  <!--  -->
  <el-dialog title="" v-model="dialogFormVisible_moneypay" size="tiny" >
    <el-form ref="form" :model="form" label-width="100px">
      <i class="fa fa-user-circle-o fa-2x" aria-hidden="true"></i> <span>{{form.name}}</span>
      <br><br>
      <el-form-item label="" class="lleft">
        <el-input v-model="form.money"></el-input>
      </el-form-item>
      <el-form-item label="">
      <el-select v-model="form.style" placeholder="">
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
      </el-select>
      </el-form-item>
      <el-form-item label="">
      <el-select v-model="value" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="center">
        <el-button type="primary" @click="handleSave" :loading="editLoading"></el-button>
        <el-button @click="dialogFormVisible_moneypay = false"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>  
  <!--  -->
  <el-dialog title="" v-model="dialogFormVisible_moneypayagain" size="tiny" >
    <el-form ref="form" :model="form" label-width="100px">
      <div class="left"><i class="fa fa-id-card fa-2x" aria-hidden="true"></i><span class="space">{{form.account}}</span></div>
      <div  class="right"><i class="fa fa-usd  fa-2x" aria-hidden="true"></i><span class="content_size">{{form.money}}</span></div>  <br><br><br>
    <el-form-item label=""><span>{{form.name}}</span> </el-form-item>
    <el-form-item label="" class="lleft">
      <el-input v-model="paymoney"></el-input>
    </el-form-item>
    <el-form-item label="">
    <el-select v-model="value" placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item class="center">
    <template scope="scope">
      <el-button type="primary" @click="handleSave_moneypayagain(scope.$index, scope.row)" :loading="editLoading"></el-button>
      <el-button @click="dialogFormVisible_moneypayagain = false"></el-button>
    </template>
    </el-form-item>
    </el-form>
  </el-dialog>
  <!--  -->
  <el-dialog title="" v-model="dialogFormVisible_changestyle" size="tiny" >
    <el-form ref="form" :model="form" label-width="120px">
      <div class="left"><i class="fa fa-id-card fa-2x" aria-hidden="true"></i><span class="space">{{form.account}}</span></div>
      <div  class="right"><i class="fa fa-usd  fa-2x" aria-hidden="true"></i><span class="content_size">{{form.money}}</span></div>  <br><br><br>
    <el-form-item label=""><span>{{form.name}}</span> </el-form-item>
    <el-form-item label=""><span>{{form.style}}</span> </el-form-item>
     <el-form-item label="">
      <el-select v-model="changestyle" placeholder="">
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
      </el-select>
      </el-form-item>
    <el-form-item class="center">
    <template scope="scope">
      <el-button type="primary" @click="handleSave_changestyle()" :loading="editLoading"></el-button>
      <el-button @click="dialogFormVisible_changestyle = false"></el-button>
    </template>
    </el-form-item>
    </el-form>
  </el-dialog>   
  <!--  -->
  <el-dialog title="" v-model="dialogFormVisible_familyShare" size="tiny" >
    <el-form ref="form" :model="form" label-width="120px">
      <div class="left"><i class="fa fa-id-card fa-2x" aria-hidden="true"></i><span class="space">{{form.account}}</span></div>
      <div  class="right"><i class="fa fa-usd  fa-2x" aria-hidden="true"></i><span class="content_size">{{form.money}}</span></div>  <br><br><br>
    <el-form-item label=""><span>{{form.name}}</span> </el-form-item>
    <el-form-item label=""><span>{{form.share}}</span><!-- <i class="fa fa-trash-o" aria-hidden="true"></i> -->
</el-form-item>
    <el-form-item label="">
      <el-input v-model="changefamilyShare" placeholder=""></el-input>
    </el-form-item>
    <el-form-item class="center">
    <template scope="scope">
      <el-button type="primary" @click="handleSave_familyShare()" :loading="editLoading"></el-button>
      <el-button @click="dialogFormVisible_familyShare = false"></el-button>
    </template>
    </el-form-item>
    </el-form>
  </el-dialog>   
  <!--  -->
  <div class="block">
     <el-pagination
       @size-change="handleSizeChange"
       @current-change="handleCurrentChange"
       :current-page="currentPage1"
       :page-sizes="[10, 20, 30, 40]"
       :page-size="100"
       layout="total, sizes, prev, pager, next, jumper"
       :total="tableData.length">
     </el-pagination>
  </div>
  </div>
  </div>
</template>
<script >

export default {
  name: 'account',
  data () {
    return {
      msg: '>',
      dialogFormVisible_moneypay:false,
      dialogFormVisible_moneypayagain:false,
      dialogFormVisible_changestyle:false,
      dialogFormVisible_familyShare:false,
      editLoading:false,//
      currentPage1: 5,
      activeIndex: '1',
      value: '',
      paymoney:'',
      changestyle:'',
      changefamilyShare:'',
      formInline: {
         user: '',
         region: '',
         account:'',
      },
     form: {
        account:'',
        style: '',
        name: '',
        share:'',
        money:'',
        deadline:'',
        type:'',

      },
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }],
      tableData:[
                  {
                      account:'440982',
                      style: '',
                      name: '',
                      share:'marry',
                      money:null,
                      deadline:'2017-9-1',
                  },
                   {
                      account:'4409821',
                      style: '',
                      name: '',
                      share:'',
                      money:85,
                      deadline:'2017-9-2',
                  },
                   {
                      account:'440987',
                      style: '',
                      name: '',
                      share:'',
                      money:56,
                      deadline:'2017-9-7',
                  },
                   {
                      account:'440952',
                      style: '',
                      name: '',
                      share:'',
                      money:85,
                      deadline:'2017-8-1',
                  },
                   {
                      account:'440989',
                      style: '',
                      name: '',
                      share:'',
                      money:28,
                      deadline:'2017-9-1',
                  },
                ]
      }
    },
  methods: {
      onSubmit() {
       this.$message('~');
      },
     //
      accountPay (index, row) {
      let money=this.tableData[index].money;
      if(money===null){
       this.dialogFormVisible_moneypay = true;
      }
      else{
       this.paymoney=null;
       this.dialogFormVisible_moneypayagain = true;
      }
       this.form = Object.assign({}, row);
       this.table_index = index;
      },
      //
      styleChange(index, row){
       this.changestyle='';
       this.dialogFormVisible_changestyle = true;
       this.form = Object.assign({}, row);
       this.table_index = index;
      },
      //
      familyShare(index, row){
       this.changefamilyShare='';
       this.dialogFormVisible_familyShare = true;
       this.form = Object.assign({}, row);
       this.table_index = index;
      },
      //
      handleSave () {
       this.$confirm('', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         cancelButtonClass: 'cancel'
       }).then(() => {
        this.editLoading = true;//
        this.tableData[this.table_index] = this.form;
        this.tableData.splice(this.table_index, 1, this.form);
        this.$message({
           message: "",
           type: 'success'
         });
         this.editLoading = false;
         this.dialogFormVisible_moneypay = false;
       })
      },
      //
      handleSave_moneypayagain (index,row) {
       this.$confirm('', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         cancelButtonClass: 'cancel'
       }).then(() => {
        this.form.money=parseInt(this.paymoney)+this.form.money;
        this.tableData[this.table_index] = this.form;
        this.tableData.splice(this.table_index, 1, this.form);
        this.$message({
           message: "",
           type: 'success'
         });
         this.editLoading = false;
         this.dialogFormVisible_moneypayagain = false;
        })
      },
      //
      handleSave_changestyle () {
       this.$confirm('', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         cancelButtonClass: 'cancel'
       }).then(() => {
        this.editLoading = true;//
        this.form.style=this.changestyle;
        this.tableData[this.table_index] = this.form;
        this.tableData.splice(this.table_index, 1, this.form);
        this.$message({
           message: "",
           type: 'success'
         });
         this.editLoading = false;
         this.dialogFormVisible_changestyle = false;
       })
      },
       //
      handleSave_familyShare() {
       this.$confirm('', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         cancelButtonClass: 'cancel'
       }).then(() => {
        this.editLoading = true;//
        this.form.share=(this.changefamilyShare+','+this.form.share);
        this.tableData[this.table_index] = this.form;
        this.tableData.splice(this.table_index, 1, this.form);
        this.$message({
           message: "",
           type: 'success'
         });
         this.editLoading = false;
         this.dialogFormVisible_familyShare = false;
       })
      },
     //
      handleDelete (index, row) {
       this.tableData.splice(index, 1);
       this.$message({
         message: "",
         type: 'success'
       });
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      }
    
  }
}
</script>
<style >
.account{
  padding: 20px;
}
.account .content{
 background: white;
}
.account .block{
  text-align: center;
}
.account .center{
  text-align: center;
}
.account .left,.right{
  width: 50%;
}
.account .left{
  float: left;
}
.account .right{
  float: right;
}
.account .lleft{
  text-align: left;
}
.account .el-form-item__content {
  font-size: 23px;
}
.account .content_size{
  font-size: 20px;
}
.el-dialog__header{
  background: #f6f6f6;
  padding-bottom: 10px;
}
</style>



